export default {
	data() {
		return {
			phone:'',
			password:''
		};
	},
	methods: {
		register() {
			const {phone,password} = this
			let arr;
			if(!uni.getStorageSync('users')){
				arr = []
			}else{
				arr = uni.getStorageSync('users')
			}
			let TEL_REGEXP = /^1([38]\d|5[0-35-9]|7[3678])\d{8}$/;
			if(TEL_REGEXP.test(phone) && password.length>=6){
				arr.forEach(item=>{
					if(item.phone = phone){
						uni.showToast({
							title:'该账户已注册',
							icon:'none',
							duration:1000
						})
					}else{
						arr.push({phone:phone,password:password})
						uni.setStorageSync('users',arr)
						uni.navigateTo({
							url:'/pages/login/login'
						})
					}
				})
				
			}
			if(!TEL_REGEXP.test(phone)){
				uni.showToast({
					title:'请输入正确的手机号',
					icon:'none',
					duration:1000
				})
			}
			if(password.length<6){
				uni.showToast({
					title:'密码不得小于6位',
					icon:'none',
					duration:1000
				})
			}
			
		}
	},
}
